<template>
    <div @click="handleClick" class="ui-table__img">
        <div class="ui-image--static" v-show="src && !isComplete" :style="{width, height}"></div>
        <img v-show="false" :width="width" :height="height" :src="src" alt="" ref="myImg">
        <div v-if="isComplete" :style="{width, height, background: `url(${errorSrc || src}) no-repeat center/contain`}"></div>
    </div>
</template>

<script>
    export default {
        name: "LoadingImg",
        props: {
            src: {
                type: String,
                default: ''
            },
            width: {
                type: String,
                default: '50px'
            },
            height: {
                type: String,
                default: '50px'
            }
        },
        data() {
            return {
                isComplete: false,
                errorSrc: ''
            }
        },
        methods: {
            handleClick(e) {
                this.$emit('click', e)
            }
        },
        mounted() {
            const vm = this;
            this.$nextTick(() => {

                this.$refs.myImg.addEventListener('load', function() {
                    vm.isComplete = true;
                    // console.log(vm.$refs.myImg.clientHeight);
                    // console.log(vm.$refs.myImg.clientWidth);
                    console.log(window.getComputedStyle(vm.$refs.myImg, null).height);
                });

                this.$refs.myImg.addEventListener('error', function() {
                    vm.isComplete = true;
                    vm.errorSrc = require('../assets/image/img_error.png');
                })
            })
        }
    }
</script>

<style scoped>
    .ui-table__img {
        /*background: #eeeeee;*/
        border: 1px solid #e6e6e6;
        display: inline-block;
    }

    .ui-table__img img {
        width: 100%;
        height: 100%;
    }

    .ui-image--static {
        background: url("../assets/image/img_loading.gif") no-repeat center/contain;
    }
</style>


