            border-collapse: collapse;
            border-bottom: 1px solid @border-color;

            &:last-child {
                border: 0 none;
            }

            tbody tr:last-child td {
                border-bottom: 0 none;
            }

            tbody td {
                color: #333333;
                font-size: 14px;
                font-weight: 100;
                width: 15%;
                border-bottom: 1px solid @border-color;
                padding: 20px 0;

                &:first-child {
                    width: 40%;
                    padding-left: 18px;
                    box-sizing: border-box;
                }
            }

            .ui-table_content--item {
                height: 70px;

                .el-input {
                    width: 90px;
                }
            }

            .ui-media {
                position: relative;

                .ui-media--img {
                    position: absolute;
                    top: 0;
                    left: 0;
                }

                .ui-media--img img {
                    width: 70px;
                    height: 70px;
                }

                .ui-media--content {
                    margin-left: 82px;
                    padding-right: 60px;
                    position: relative;

                    p {
                        /*line-height: 1.3em;*/
                        /*color: #666666;*/
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 2;
                        overflow: hidden;
                        font-size: 12px;
                    }

                    span {
                        color: #999999;
                        font-size: 12px;
                    }

                    .ui-media--content--icon {
                        margin-top: 4px;
                        position: relative;
                        display: inline-block;
                        width: @icon-img;
                        height: @icon-img;
                        border: 1px solid @border-color;
                        background: url(https://cbu01.alicdn.com/img/ibank/2018/364/402/9335204463_327185066.32x32.jpg) no-repeat center;
                    }

                    .ui-media--content--icon > img {
                        margin-top: 5px;
                    }

                    .ui-media--content--icon:hover .ui-media--content--down {
                        opacity: 1;
                        visibility: unset;
                        /*max-height: unset;*/
                    }

                    .ui-media--content--down {

                        &::before {
                            display: block;
                            content: '';
                            position: absolute;
                            width: 10px;
                            height: 10px;
                            background: #FFFFFF;
                            transform: rotateZ(45deg);
                            top: -4px;
                            left: 5px;
                            box-shadow: 0 0 6px 1px #c9c9c9;
                        }

                        &::after {
                            content: '';
                            position: absolute;
                            width: 24px;
                            height: 16px;
                            background: #FFFFFF;
                            top: 0;
                            left: 0;
                        }

                        transition: .2s all linear;
                        box-shadow: 0 0 6px -1px #c9c9c9;
                        opacity: 0;
                        visibility: hidden;
                        width: 510px;
                        background: #FFFFFF;
                        border: 1px solid @border-color;
                        padding: 10px 0 0 10px;
                        position: absolute;
                        top: 40px;
                        z-index: 1;
                        /*max-height: 100px;*/

                        img {
                            width: @icon-img;
                            height: @icon-img;
                            vertical-align: middle;
                        }

                        li {
                            position: relative;
                            z-index: 2;
                            float: left;
                            color: #fc8246;
                            border: 1px solid @border-color;
                            margin-bottom: 5px;
                            margin-right: 10px;
                            cursor: pointer;
                            padding-right: 5px;

                            &.ui-active {
                                border: 1px solid @active-color;
                            }
                        }
                    }
                }
            }
        }

        .ui-table_content--row_count {
            position: absolute;
            bottom: 10px;
            right: 30px;

            span {
                display: inline-block;
                width: 150px;
                color: #FC7019;

                &:nth-child(3n) {
                    width: 230px;
                }
            }
        }
    }
}

/deep/ .el-dialog__body {
    padding-top: 10px
}

.ui-table__img img {
    max-width: 70px;
    max-height: 90px;
}
</style>



